.Card {
    min-width: 150px;
    height: 50px;
    margin: 5px;
    -webkit-box-shadow: 0 2px 3px #eee;
            box-shadow: 0 2px 3px #eee;
    background: #fff;
    padding: 10px 20px;
    cursor: pointer;
  }
  
  .Card.-selected {
    border: 2px solid blue;
  }
  
  .Card.-match {
    position: relative;
    -webkit-animation: hideMatchedCards 1.7s ease forwards;
            animation: hideMatchedCards 1.7s ease forwards;
  }
  
  @-webkit-keyframes hideMatchedCards {
    from {
      left: 0;
    }
    to {
      left: -200%;
    }
  }
  
  @keyframes hideMatchedCards {
    from {
      left: 0;
    }
    to {
      left: -200%;
    }
  }
  


