.InputContainer {
  width: 100%;
  padding: 10px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.Label {
  font-weight: 900;
  display: block;
  margin: 8px;
}

.Input {
  outline: none;
  border: 1px solid #ccc;
  padding: 6px 10px;
  display: block;
  width: 100%;
}

.Input:focus {
  background: #ccc;
}

.Invalid {
  border: 1px solid red;
  background: #fda49a;
}

.validation-error {
  color: red;
  margin: 0 5px;
}



