.Toolbar {
  width: 100%;
  height: 56px;
  position: fixed;
  top: 0;
  left: 0;
  background: #703b09;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  padding: 0 20px;
  z-index: 90;
  -ms-flex-align: center;
      align-items: center;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

.Toolbar nav {
  height: 100%;
}

.Toolbar > .DrawerToggleButton > div {
  background-color: #fff;
}

@media (max-width: 500px) {
  .Toolbar > nav {
    display: none;
  }
}



