.DrawerToggleButton {
  width: 40px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: column;
      flex-flow: column;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -ms-flex-align: center;
      align-items: center;
  padding: 10px 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  cursor: pointer;
  overflow: hidden;
}

.DrawerToggleButton > div {
  width: 90%;
  height: 3px;
  margin-bottom: 5px;
  border-radius: 5px;
  background-color: #000;
  -webkit-transition: -webkit-transform 1s ease;
  transition: -webkit-transform 1s ease;
  -o-transition: transform 1s ease;
  transition: transform 1s ease;
  transition: transform 1s ease, -webkit-transform 1s ease;
}

.DrawerToggleButton:hover > div:first-child {
  -webkit-transform: translateY(260%) rotate(45deg);
      -ms-transform: translateY(260%) rotate(45deg);
          transform: translateY(260%) rotate(45deg);
}

.DrawerToggleButton:hover > div:nth-child(2) {
  -webkit-transform: translateX(-150%);
      -ms-transform: translateX(-150%);
          transform: translateX(-150%); 
}

.DrawerToggleButton:hover > div:last-child {
  -webkit-transform: translateY(-260%) rotate(-45deg);
      -ms-transform: translateY(-260%) rotate(-45deg);
          transform: translateY(-260%) rotate(-45deg);
}

@media (min-width: 500px) {
  .DrawerToggleButton {
    display: none;
  }
}



